.gridWrapper {
  position: absolute;
  left: 0;
  right: 0;
  margin: auto;
  max-width: 480px;
  max-height: 600px; }

.Grid {
  display: inline-block;
  position: relative;
  min-width: 480px;
  min-height: 600px; }
  .Grid .row {
    position: relative; }

.controls button {
  margin: 5px; }

.controls--tick {
  display: block;
  width: 100%; }

.cell {
  width: 10%;
  display: inline-block;
  height: 30px; }
  .cell--white {
    background-color: #2b2b2b; }



